import "./index.scss"
import { List , Button } from 'antd-mobile';
import { history } from "&/history"

const Item = List.Item;
const Brief = Item.Brief;


import Head from "~/components/Head"
import Foot from "~/components/Foot"

export default class Mine extends Component{

    TLogin=()=>{
    history.push("./login")
    sessionStorage.token = "";
      

    }

    gotobookcase=()=>{
        history.push("./bookcase")
    }
    gotoTopup=()=>{
        history.push("./topup")
    }
    gotoConsumption=()=>{
        history.push("./consumption")
    }

    render(){
        return(
            <div>
                <Head title="个人中心" show={true}></Head>
                
                    <div className="my-top">
                        <div>
                        <img className="top_img" style={{width:"0.8rem" , height:"0.8rem"}} src={require("@/assets/images/touxiang2.png")} alt="" />
                        </div>
                        <div className="top_txt">
                            <p style={{width:"4rem" , height:"0.5rem" ,overflow:"hidden" }}>
                                书友:{}
                            </p>
                            <p>
                                id:        123456789
                            </p>
                        </div>
                        <div className="top_btn">
                            修改
                        </div>
                    </div>
                 
               
                <div>
                <List renderHeader={() => ''} className="my-list">
                <Item
                arrow="horizontal"
                onClick={() => {}}
                >
                <Brief>我的余额</Brief>
                </Item>
                <Item
                arrow="horizontal"
              
                multipleLine
                onClick={() => {}}
                >
                <Brief>书豆充值</Brief>
                </Item>
                <Item
                arrow="horizontal"
              
                multipleLine
                onClick={this.gotoTopup}
                >
                <Brief>充值记录</Brief>
                </Item>
                <Item
                arrow="horizontal"
                
                multipleLine
                onClick={this.gotoConsumption}
                >
                <Brief>消费记录</Brief>
                </Item>
                </List>
                </div>

                <div>
                <List renderHeader={() => ''} className="my-list">
             
                <Item
                arrow="horizontal"
               
                multipleLine
                onClick={this.gotobookcase}
                >
                <Brief>我的书架</Brief>
                </Item>
                <Item
                arrow="horizontal"
              
                multipleLine
                onClick={() => {}}
                >
                <Brief>密码管理</Brief>
                </Item>
                <Item
                arrow="horizontal"
               
                multipleLine
                onClick={this.TLogin}
                >
                <Brief>退出账号</Brief>
                </Item>
               
                </List>
                </div>

                <Foot></Foot>
            </div>
        )
    }
}